
  import EncounterMixinVue from "@/views/EncounterMixin.vue";
  import { FieldType } from "@/components/Forms/BaseFormElements";
  import { defineComponent } from "vue";
  import { IonPage } from "@ionic/vue";
  import { Field, Option } from "@/components/Forms/FieldInterface";
  import { LabOrderService } from "@/apps/ART/services/lab_order_service";
  import { OrderService } from "@/services/order_service";
  import { HtsDbsOrder } from "../../services/hts_dbs_order";
  
  export default defineComponent({
    components: { IonPage },
    mixins: [EncounterMixinVue],
    data: () => ({
      service: {} as HtsDbsOrder,
    }),
    watch: {
      ready: {
        handler(ready: boolean) {
          if (ready) {
            this.service = new HtsDbsOrder(
              this.patientID,
              this.providerID
            );
            this.fields = [
              this.isDBSsampleCollected(),
              this.dbsSpecimenID()
            ];
          }
        },
        immediate: true,
      },
    },
    methods: {
      onFinish(formData: any, computedData: any) {
        let labOrders: any = undefined;
        if (formData.dbs_sample_collection === 'Yes') {
          labOrders = this.buildDBSOrder(formData.dbs_specimen_id)
        }
        this.saveFormEncounterAndProceed({
          encounterTypeID: this.service.encounterTypeID,
          formData,
          computedData,
          labOrders,
          callback: async () => {
            if (labOrders) {
              await this.createDBSOrder(formData.dbs_specimen_id)
            }
          }
        })
      },
      buildDBSOrder(accessionNumber: string) {
        return [{
          concept_id: HtsDbsOrder.getCachedConceptID('HIV viral load'),
          specimenConcept: HtsDbsOrder.getCachedConceptID("DBS (Free drop to DBS card)", true),
          accessionNumber,
          reason: 'Routine',
        }]
      },
      async createDBSOrder(specimenID: string) {
        const order = new LabOrderService(this.patientID, this.providerID)
        const encounter = await order.createEncounter()
        if (encounter) {
          const orders = OrderService.buildLabOrders(encounter, this.buildDBSOrder(specimenID))
          await OrderService.saveOrdersArray(encounter?.encounter_id, orders)
        }
      },
      isDBSsampleCollected(): Field {
        return {
          id: "dbs_sample_collection",
          helpText: "DBS Sample",
          type: FieldType.TT_YES_NO,
          isRequired: () => true,
          finalComputedValue: (v: any) => {
            return {
              obs: this.service.buildValueCoded("Is DBS Sample Collected", v),
            };
          },
          options: () => {
            return [
              {
                label: "Is DBS Sample Collected?",
                value: "",
                values: this.yesNoOptions(),
              },
            ];
          },
        };
      },
      dbsSpecimenID(): Field {
        return {
          id: "dbs_specimen_id",
          helpText: "DBS Specimen ID",
          type: FieldType.TT_BARCODE,
          requireNext: false,
          condition: (f: any) => /yes/i.test(`${f.dbs_sample_collection}`),
          finalComputedValue: (v: Option) => {
            return {
              obs: this.service.buildValueText("DBS Specimen ID", `${v}`),
            };
          },
          config: {
            hiddenFooterBtns: [
              'Clear'
            ]
          },
          isRequired: () => true
        };
      },
    },
  });
  